import header, {
  shadowDrop,
  pickGravatar,
  stopAnimationOnResize,
  navLinkFade,
} from "./header";
import gravatars, { loadImages, IMAGE_SIZE } from "./gravatars";
import { openModal, initModal } from "./modal";

export function setupListeners(window, gravatarContainer) {
  window.document.getElementById("logo").addEventListener("click", (e) => {
    document.documentElement.scrollTop = 0;
  });

  window.document.getElementById("header").addEventListener("click", (e) => {
    pickGravatar(e);
    gravatars(window, gravatarContainer);
    setTimeout(function () {
      loadImages(IMAGE_SIZE);
    }, 0);
  });

  window.addEventListener("scroll", () => {
    gravatars(window, gravatarContainer);
    setTimeout(function () {
      loadImages(IMAGE_SIZE);
    }, 0);
    shadowDrop(window);
  });

  window.addEventListener("resize", () => {
    gravatars(window, gravatarContainer);
    setTimeout(function () {
      loadImages(IMAGE_SIZE);
    }, 0);
    stopAnimationOnResize(window);
  });

  gravatarContainer.addEventListener("click", (e) => {
    openModal(e, window);
  });

  window.addEventListener("load", () => {
    loadImages(IMAGE_SIZE, "mp");
  });

  const menuIcon = window.document.querySelector(".header__menuIcon");
  menuIcon.addEventListener("click", () => {
    navLinkFade(menuIcon);
  });
}

export function init(window, root) {
  root.appendChild(header(window));

  const overlay = window.document.createElement("div");
  overlay.setAttribute("id", "overlay");

  const gravatarContainer = window.document.createElement("div");
  gravatarContainer.classList.add("identicon");
  gravatarContainer.setAttribute("id", "gravatars");

  root.appendChild(gravatarContainer);
  root.appendChild(overlay);
  root.appendChild(initModal(window));

  gravatars(window, gravatarContainer);
  setupListeners(window, gravatarContainer);
}
