export function openModal(e, window) {
  if (!e.target.classList.contains("gravatars__gravatar")) return;
  const gravatartarget = e.target;
  const text = document.getElementById("emailAddress");
  const overlay = window.document.getElementById("overlay");

  gravatartarget.classList.toggle("gravatars__gravatar--is-highlighted");
  text.textContent = gravatartarget.id;

  window.document.getElementById("modal").classList.add("modal--active");

  overlay.classList.add("modal__overlay--active");

  overlay.addEventListener("click", () => {
    closeModal(gravatartarget);
  });

  window.document
    .getElementById("closeButton")
    .addEventListener("click", () => {
      closeModal(gravatartarget);
    });
}
export function closeModal(gravatartarget) {
  gravatartarget.classList.remove("gravatars__gravatar--is-highlighted");
  window.document.getElementById("modal").classList.remove("modal--active");
  window.document
    .getElementById("overlay")
    .classList.remove("modal__overlay--active");
}
export function initModal(window) {
  const modalElem = window.document.createElement("div");
  modalElem.setAttribute("id", "modal");
  modalElem.classList.add("modal");

  modalElem.innerHTML = `
  <div class="modal__header">
  <div class="modal__title">Email:</div>
  <div id="closeButton" class="modal__close"></div>
  </div>
  <div class="modal__body" id="emailAddress">email</div>
  `;

  return modalElem;
} //
