import logo from "./logo.jpg";

export default function (window) {
  const headerElem = window.document.createElement("header");
  headerElem.setAttribute("id", "header");
  headerElem.classList.add("header");
  headerElem.innerHTML = `
  <img src="${logo}" id="logo" class="header__logo">
  <div class="header__navLinks" id="navLinks">
    <img id="identicon" src="https://www.gravatar.com/avatar/00000000000000000000000000000000?d=identicon&amp;f=y" class="header__thumbnail ">
    <img id="monsterid" src="https://www.gravatar.com/avatar/00000000000000000000000000000000?d=monsterid&amp;f=y" class="header__thumbnail ">
    <img id="wavatar" src="https://www.gravatar.com/avatar/00000000000000000000000000000000?d=wavatar&amp;f=y" class="header__thumbnail ">
    <img id="retro" src="https://www.gravatar.com/avatar/00000000000000000000000000000000?d=retro&amp;f=y" class="header__thumbnail ">
    <img id="robohash" src="https://www.gravatar.com/avatar/00000000000000000000000000000000?d=robohash&amp;f=y" class="header__thumbnail ">
  </div>
  <div class="header__menuIcon">
    <div class="header__line1"></div>
    <div class="header__line2"></div>
    <div class="header__line3"></div>
  </div>
  `;

  return headerElem;
}

export function shadowDrop() {
  document.documentElement.scrollTop > 20
    ? window.document.getElementById("header").classList.add("header--shadow")
    : window.document
        .getElementById("header")
        .classList.remove("header--shadow");
}

