export function stopAnimationOnResize(window) {
  window.document
    .getElementById("navLinks")
    .classList.add("header__navLinks--animation-stopper");

  setTimeout(() => {
    window.document
      .getElementById("navLinks")
      .classList.remove("header__navLinks--animation-stopper");
  }, 401);
}

export function navLinkFade(menuIcon) {
  const navLinks = document.querySelector(".header__navLinks");
  navLinks.classList.toggle("header__navLinks--active");
  const navLinksimg = document.querySelectorAll(".header__navLinks img");
  navLinksimg.forEach((thumbnail, i) => {
    if (thumbnail.style.animation) {
      thumbnail.style.animation = "";
    } else {
      thumbnail.style.animation = `navLinkFade 0.5s ease forwards ${
        i / 7 + 0.5
      }s`;
    }
  });
  menuIcon.classList.toggle("header__menuIcon--active");
}
