import generateGavatarURL from "../gravatar-url";
import logo from "./logo.jpg";
import md5 from "js-md5";
export const IMAGE_SIZE = 64;
const EXTRA_ROWS = 5;

export function createImagePlaceholders(window, numberOfImages) {
  return Array.apply(null, Array(numberOfImages)).map(() =>
    gravatarPlaceholder(window)
  );
}

export function gravatarPlaceholder(window) {
  const img = new window.Image();
  img.src = logo;
  img.width = IMAGE_SIZE;
  img.height = IMAGE_SIZE;
  img.setAttribute("id", generateGavatarURL(IMAGE_SIZE));
  img.classList.toggle("gravatars__placeHolder");
  return img;
}

export function loadImages(IMAGE_SIZE) {
  const placeHolders = [
    ...window.document.querySelectorAll(".gravatars__placeHolder"),
  ];
  if (!placeHolders.length) {
    return;
  }
  const gravatarType = window.document.getElementById("gravatars").className;
  placeHolders.map((i) => {
    i.src = `http://www.gravatar.com/avatar/${md5(
      i.id
    )}?d=${gravatarType}&s=${IMAGE_SIZE}`;
    i.classList.remove("gravatars__placeHolder");
    i.classList.add("gravatars__gravatar");
  });
}

export function calculateNumberOfImages(window, root) {
  const currentImages = document
    .getElementById("gravatars")
    .querySelectorAll("img").length;
  const width = root.offsetWidth + window.scrollX;
  const height = window.innerHeight + window.scrollY;
  const imagesPerRow = Math.floor(width / IMAGE_SIZE);
  const imageRows = Math.floor(height / IMAGE_SIZE) + EXTRA_ROWS;
  return imagesPerRow * imageRows - currentImages;
}

export default function (window, root) {
  const imagesToCreate = calculateNumberOfImages(window, root);
  if (imagesToCreate <= 0) {
    return;
  }
  createImagePlaceholders(window, imagesToCreate).forEach((image) => {
    root.appendChild(image);
  });
}
